@import url('https://fonts.googleapis.com/css2?family=Space+Mono&display=swap');

html, body, #root, .app {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Space Mono', monospace;
  font-weight: lighter;
  color: white;
}

.app {
  display: flex;
}

html {
  scroll-behavior: smooth;
}

.Sidebar {
  background-color: #262626;
  width: 25%;
  min-width: 300px;
  max-width: 360px;
  height: 100%;
  text-align: center;
  position: fixed;
}

#content {
  background-image: url("./assets/images/monstera.jpg");
  background-size: 100%;
  background-repeat: repeat-y;
  flex-grow: 1;
  margin-left: 360px;
  height: fit-content;
}

main {
  backdrop-filter: blur(12px);
  margin-top: -30px;
  padding-bottom: 30px;
  background-color: rgba(255, 255, 255, .5);
  color: #262626;
}

@media screen and (min-width: 1200px) and (max-width: 1460px){
  #content {
    margin-left: 25%;
  }
}

@media screen and (max-width: 1200px) {
  #content {
    margin-left: 300px;
    min-width: 500px;
  }
}

#profile-picture {
  width: 30%;
  min-width: 200px;
  max-width: 300px;
  border-radius: 100%;
  margin-top: 50px;
}

div#container {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  width: 30%;
}

div#container * {
  width: 30px;
  margin-left: auto;
  margin-right: auto;
}

h1, h2 {
  font-weight: lighter;
}

h1 {
  font-size: 2.2em;
}

#Home, p {
  font-size: 1.2em;
}

#code {
  width: 60px;
  position: fixed;
  bottom: 20px;
  transform: translate(-50%, 0);
}

@media screen and (max-height: 600px) {
  #code {
    display: none;
  }
}

.SidebarNav{
  list-style: none;
  text-align: left;
  margin-top: 30px;
  margin-top: 20px;
  margin-left: 15%;
}

.SidebarNavRow {
  margin-bottom: 10px;
  position: relative;
  width: fit-content;
  cursor: pointer;
}

.SidebarNavRow::before {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 3px;
  bottom: -1px;
  left: 0;
  background-color: white;
  transform-origin: bottom left;
  transition: .3s;
}

.SidebarNavRow:hover::before {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.ContactMe, .More {
  height: 100vh;
  padding-top: 50px;
}

.Home {
  padding-left: 12%;
  height: 70vh;
  padding-top: 30vh;
  /* background-color: red; */
}

.AboutMe {
  text-align: center;
  padding-top: 50px;
  /* background-color: blue; */
}

.ContactMe {
  text-align: center;
}

.More {
  text-align: center;
}

.AboutCard {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0px 4px 10px 0px rgb(60, 60, 60);
  backdrop-filter: blur(10px);
  /* margin-left: auto;
  margin-right: auto; */
  margin: 100px auto;
  margin-bottom: 40px;
  width: 40%;
  min-width: 600px;
  padding: 30px 25px;
  position: relative;
  top: 25%;
  border-radius: 37px;
  text-align: left;
  line-height: 20px;
}

.AboutCardImage {
  width: 100px;
  padding-right: 12px;
}

.AboutCardHeader {
  display: flex;
}

.AboutCardHeader h1 {
  flex: 2;
}

.AboutCardHeader h2 {
  flex: 2;
}

.AboutCard li {
  line-height: 23px;
  margin-bottom: 5px;
}

.bio {
  width: 50%;
  min-width: 600px;
  margin: 100px auto;
}

.ContactCard {
  background-color: #262626bd;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(20);
  text-align: left;
  width: 450px;
  margin: 100px auto;
  padding: 30px 20px 40px 20px;
  line-height: 12px;
  font-size: .9em;
  color: white;
  margin-top: 30vh;
}

.ContactCard img {
  width: 75px;
  border-radius: 100%;
  border: 4px solid black;
}

.ResumeDownload {
  text-decoration: none;
  color: white;
}

.ResumeDownload:hover {
  color: lightgray;
  transition: .3s;
}